import React from 'react';
import {Button, Modal, Input, Form, Tabs, message, InputNumber, Divider,Breadcrumb, Table, Select, notification, Icon} from 'antd';
// import Sign from '../../../components/sign/Sign';
import styles from './Home.scss';
import {getServerListAndHardDiskList,generateAOrder} from "../../../api/Businessman";
import {hardwareTopology, serverPartList} from "../../../api/common";
import QueryFormPopup from "../../../components/queryFormPopup/QueryFormPopup";
const FormItem = Form.Item;
const Option = Select.Option;
const TabPane = Tabs.TabPane;
const indexTabData = [
    {id:"EXCLUSIVE", name:"独享私有云",key:0},
    // {id:"TRUSTEESHIP", name:'托管私有云',key:1},
];
const  equipmentName =[
    {id:"gpu", name:'GPU服务器',key:0,listName:"gpuServerList",selectKey:0},
    {id:"store", name:'存储服务器',key:1,listName:"storeServerList",selectKey:0},
    {id:"cpu", name:'CPU服务器',key:2,listName:"cpuServerList",selectKey:0},
    {id:"ib", name:'IB交换机',key:3,listName:"ibServerList",selectKey:0},
    {id:"可选硬盘", name:'硬盘',key:4,listName:"partEntityList",selectKey:0},
];

class Home extends React.Component {
constructor(props){
    super(props);
    this.state = {
        tabData:indexTabData,
        // tabId:this.props.tabId,
        allHdList:[],
        allSeverList:[],
        detailRecord:[],
        detailVisible:false,
        tableLoading:false,
        aSeverParts:[],
        queryFormModalVisible:false,
        tabKey:0
    };
}
    async componentDidMount () {
        this.props.form.resetFields();
        console.log(123);
        this.setState({
            tableLoading:true
        });
        await this.getAllSeverList();
        let dataSource=[];
        equipmentName.map((item,index)=>{
            dataSource.push({
                key:index,
                equipment:item.name,
                configure:this.state.allSeverList[item.listName],
                // allSeverHdList:this.state.allSeverHdList[index]
            });
            return dataSource;
        });
        console.log(dataSource);

            const columns = [{
                title: '设备',
                dataIndex: 'equipment',
                key: 'equipment',
            }, {
                title: '配置',
                dataIndex: 'configure',
                render: (text,record) => (
                    <Select
                        className={styles.select}
                        size="large"
                        defaultValue={record.configure?record.configure[0].name:""}
                        onSelect={(value,option)=>this.selectQuery(value,option,record.key)}
                        key={record.key}
                        showSearch
                        optionFilterProp="children"
                    >
                        {record.configure?record.configure.map((item,index)=>{return(<Option key={index}>{item.name}</Option>)}):""}

                    </Select>
                )
            },{
                title: '操作',
                key: 'operator',
                render: (text,record) => (
                    <span>
                <a  onClick={()=>this.detailMessage(record)}>详情</a>
           </span>
                ),
            }
            ];
            this.setState({
                dataSource:dataSource,
                columns:columns,
                tableLoading:false
            })

    }
    //tab切换
    tabChange =async(activeKey)=>{
        this.props.form.resetFields();
    await this.setState({
        tabKey:activeKey
    })
    };
    //下载拓扑图
    getHardwareTopology=async(orderInfo)=>{
        try {
            const result = await  hardwareTopology({
                gpuNumber:orderInfo.ordersEntity.gupServerNumber,
                storeNumber:orderInfo.ordersEntity.storageServerNumber,
                cpuNumber:orderInfo.ordersEntity.cpuServerNumber,
                userNumber:orderInfo.ordersEntity.userNumber,
                ibNumber:orderInfo.ordersEntity.ibNumber,

            });
            console.log(result );
            if(result.errorCode){
            }
            else{
                let hardwareTopologyUrl=window.URL.createObjectURL(result);
                console.log(hardwareTopologyUrl);
                await this.setState({
                    hardwareTopologyUrl:hardwareTopologyUrl
                })
            }
        }

        catch (e) {

        }
    };
    //生成一个订单
    generateAOrder=async(fromInfo)=>{
        try {
            let params=this.state.tabData[this.state.tabKey].id==="TRUSTEESHIP"?{
                userRealityProportion:`${fromInfo.actualConcurrentUserRatio}`,
                userQueueProportion:fromInfo.userQueueProportion,
                userStorage:`${fromInfo.userStorageQuota}`,
                bandwidthPrice:`${fromInfo.bandWidthPrice}`,
                opsPriceCoefficient:`${fromInfo.opsPrice}`,
                userNumber:fromInfo.studentNumber,
                scheduleClassesEverydayNumber:`${fromInfo.scheduleCount}`,
                schooldaysPerWeekNumber:`${fromInfo.classDayNumber}`,
                systemUsersTotal:fromInfo.requireNumber,
                serviceCycle:fromInfo.seniority,
                hardDiskId:`${this.state.allSeverList.partEntityList[equipmentName[4].selectKey].id}`,
                cpuServerNumber:`${fromInfo.CPUServersNumber}`,
                gpuId:`${this.state.allSeverList.gpuServerList[equipmentName[0].selectKey].id}`,
                storageId:`${this.state.allSeverList.storeServerList[equipmentName[1].selectKey].id}`,
                cpuId:`${this.state.allSeverList.cpuServerList[equipmentName[2].selectKey].id}`,
                ibId:`${this.state.allSeverList.ibServerList[equipmentName[3].selectKey].id}`,
                rackLeasedPrice:`${fromInfo.CabinetRental}`
            }:{
                userRealityProportion:`${fromInfo.actualConcurrentUserRatio}`,
                userQueueProportion:fromInfo.userQueueProportion,
                userStorage:`${fromInfo.userStorageQuota}`,
                bandwidthPrice:`${fromInfo.bandWidthPrice}`,
                opsPriceCoefficient:`${fromInfo.opsPrice}`,
                userNumber:fromInfo.studentNumber,
                scheduleClassesEverydayNumber:`${fromInfo.scheduleCount}`,
                schooldaysPerWeekNumber:`${fromInfo.classDayNumber}`,
                systemUsersTotal:fromInfo.requireNumber,
                serviceCycle:fromInfo.seniority,
                hardDiskId:`${this.state.allSeverList.partEntityList[equipmentName[4].selectKey].id}`,
                cpuServerNumber:`${fromInfo.CPUServersNumber}`,
                gpuId:`${this.state.allSeverList.gpuServerList[equipmentName[0].selectKey].id}`,
                storageId:`${this.state.allSeverList.storeServerList[equipmentName[1].selectKey].id}`,
                cpuId:`${this.state.allSeverList.cpuServerList[equipmentName[2].selectKey].id}`,
                ibId:`${this.state.allSeverList.ibServerList[equipmentName[3].selectKey].id}`,
            };
            const result=await generateAOrder(params);
            console.log(result);
            if(result.errorCode===1000){
                await this.getHardwareTopology(result.data);
                this.setState({
                    orderInfo:result.data
                })
            }
            else{
                notification.info({
                    message: "生成订单失败"
                })
            }
        }
        catch (e) {

        }
    };
    //比较输入的值是否超过系统最大用户数
    compareToMaxRequest=(rule, value, callback) => {
        const form = this.props.form;
        if (value && value > (form.getFieldValue("studentNumber")*form.getFieldValue("scheduleCount")*form.getFieldValue("classDayNumber"))) {
            callback('输入的值大于系统最大用户数!');
        } else {
            callback();
        }
    };
    //获取一个服务器所有零件信息
    getPartListByServerIdNoPage=async(id)=>{
        try {
            const result = await serverPartList({
                serverId:id
            });
              console.log(result);
            if (result.errorCode === 1000) {
                this.setState({
                    aSeverParts:result.data
                });
            } else {
                notification.info({
                    message: result.errorMessage
                })
            }
        } catch (e) {
            // message.error("获取失败")
            console.log("获取失败");
        }
    };
    //获取服务器和硬件列表
    getAllSeverList= async()=>{
        try {
            const info=  await getServerListAndHardDiskList({
                partType:"可选硬盘",
                gpuType:"gpu",
                cpuType:"cpu",
                ibType:"ib",
                storeType:"store",
            });
            if(info.errorCode===1000) {
                this.setState({
                    allSeverList:info.data
                })
            }
            else{
                message.error("服务器列表加载失败")
            }
        }
        catch (e) {
            console.log("服务器异常");
        }
    };
    submitForm(){
        this.props.form.validateFieldsAndScroll(async (err, fieldsValue) => {
            if (!err) {
                fieldsValue.actualConcurrentUserRatio=fieldsValue.actualConcurrentUserRatio==null?"0.6":fieldsValue.actualConcurrentUserRatio;
                fieldsValue.userStorageQuota=fieldsValue.userStorageQuota===""?"2":fieldsValue.userStorageQuota;
                fieldsValue.CPUServersNumber=fieldsValue.CPUServersNumber==null?"1":fieldsValue.CPUServersNumber;
                if(this.state.tabData[this.state.tabKey].id==="TRUSTEESHIP"){
                    fieldsValue.bandWidthPrice=fieldsValue.bandWidthPrice===""?"120":fieldsValue.bandWidthPrice;
                    fieldsValue.opsPrice=fieldsValue.opsPrice==null?"7470":fieldsValue.opsPrice;
                    fieldsValue.CabinetRental=fieldsValue.CabinetRental==null?"132288":fieldsValue.CabinetRental;
                }
                else{
                    fieldsValue.bandWidthPrice=fieldsValue.bandWidthPrice===""?"0":fieldsValue.bandWidthPrice;
                    fieldsValue.opsPrice=fieldsValue.opsPrice==null?"10":fieldsValue.opsPrice;
                }
                fieldsValue.userQueueProportion=`${1-fieldsValue.actualConcurrentUserRatio}`;
                console.log(fieldsValue.userQueueProportion);
                console.log(fieldsValue);
                console.log(this.state.allSeverList);
               await this.generateAOrder(fieldsValue);
               await this.setState({
                   fieldsValue:fieldsValue,
                   queryFormModalVisible:true,
                     addQueryForm:true
               });

            }
            else{
                console.log(fieldsValue);
            }
        })
    }
    //点击详情弹出对话框
    detailMessage=async(record)=>{
        console.log(this.state.aSeverParts);
        console.log(record.configure[0].name);
        console.log(this.state.selectValue);
        console.log(record);
        console.log(record.configure[equipmentName[record.key].selectKey].id);
        console.log(equipmentName[record.key].selectKey);
        await this.getPartListByServerIdNoPage(record.configure[equipmentName[record.key].selectKey].id);
        await this.setState({
            detailRecord:record,
            detailVisible:true,
            selectValueName:record.configure[equipmentName[record.key].selectKey].name,
            detailPrice:record.configure[equipmentName[record.key].selectKey].price
        });
    };
    //取消弹出框
    detailClickCancel=()=>{
        this.setState({
            detailVisible:false
        })
    };
    //点击确定按钮
    detailClickOk=()=>{
        this.setState({
            detailVisible:false
        })
    };
    //下拉框值
    selectQuery= async(value,option,id)=>{
        console.log(id,value);
         equipmentName[id].selectKey=value;
        console.log(equipmentName);

    };
    //询单弹出框组组件传回的值
    queryFormModalChange=(newState)=>{
        this.setState(newState);
        if(newState.addSuccess){
            this.props.form.resetFields();
        }
    };
    //点击我要询单
    showCreateModal= async()=>{
       await this.submitForm();
    // this.setState({
    //     queryFormModalVisible:true,
    // })
    };
render() {
    const {getFieldDecorator} = this.props.form;
    const formItemLayout = {
        labelCol: {xs: { span: 6}, sm: { span: 6},},
        wrapperCol: { xs: { span: 13 }, sm: { span: 13},},
    };
    // window.localStorage.setItem("current","home");
return (
        <div className={styles.container}>
            <div className={styles.topContainer}>
        <div className={styles.top}>
       {/*<Sign name="首页"/>*/}
            <Breadcrumb>
                <Breadcrumb.Item><Icon type="shopping" theme="filled" style={{color:"#6597FF",fontSize:"24px"}}/>首页</Breadcrumb.Item>
            </Breadcrumb>
            <div className={styles.right}>
            <Button type="primary" className={styles.add}  onClick={this.showCreateModal}>我要询单</Button>
            </div>
        </div>
            </div>
            <Tabs type="card" onChange={this.tabChange}>
            {this.state.tabData.map((item)=>{
              return (<TabPane tab={item.name} key={item.key} id={item.id}>
                  {/*<PrinateCloudForm tabName={item.id}*/}
                                    {/*queryFormModalVisible={this.state.queryFormModalVisible}*/}
                                    {/*CloudFormModalChange={newState=>{this.CloudFormModalChange(newState)}}*/}
                                    {/*addQueryForm={this.state.addQueryForm}*/}
                  {/*/>*/}
                  <div className={styles.tabContainer}>
                      <div className={styles.top}>
                          <p className={styles.title}>必填信息</p>
                      </div>
                          <Form>
                              <div className={styles.top}>
                              <FormItem {...formItemLayout} label="同时上课人数">
                                  {getFieldDecorator('studentNumber', {
                                      rules: [{ required: true, message: "请输入上课人数" },
                                          {
                                              pattern: /^\+?(?:0|[1-9]\d*)$/,
                                              message:"请输入整数！"
                                          }
                                      ],
                                  })(
                                      <Input/>
                                  )}
                              </FormItem>
                              <FormItem {...formItemLayout} label="每天排课数">
                                  {getFieldDecorator('scheduleCount', {
                                      rules: [{ required: true, message: "请输入每天排课数" }, {
                                          pattern: /^[1-9]\d*$/,
                                          message:"请输入正整数！"
                                      }

                                      ],
                                  })(
                                      <InputNumber min={1} max={8} placeholder="1~8" style={{width:"100%"}}/>

                                  )}
                              </FormItem>
                              <FormItem {...formItemLayout} label="每周上课天数">
                                  {getFieldDecorator('classDayNumber', {
                                      rules: [{ required: true, message: "请输入每周上课天数" },
                                          {
                                              pattern: /^[1-9]\d*$/,
                                              message:"请输入正整数！"
                                          }
                                      ],
                                  })(
                                      <InputNumber min={1} max={7} placeholder="1~7" style={{width:"100%"}}/>
                                  )}
                              </FormItem>
                              <span className={styles.prompt}>&lt;=系统最大用户数{`${this.props.form.getFieldValue("studentNumber")*this.props.form.getFieldValue("scheduleCount")*this.props.form.getFieldValue("classDayNumber")}`} </span>
                              <FormItem {...formItemLayout} label="总账号需求数">
                                  {getFieldDecorator('requireNumber', {
                                      rules: [{ required: true, message: "请输入总账号需求数" },
                                          {
                                              validator: this.compareToMaxRequest,
                                          },
                                          {
                                              pattern: /^\+?(?:0|[1-9]\d*)$/,
                                              message:"请输入整数！"
                                          }
                                      ],
                                  })(
                                      <Input/>
                                  )}

                              </FormItem>

                              <FormItem {...formItemLayout} label="服务年限">
                                  {getFieldDecorator('seniority', {
                                      rules: [{ required: true, message: "请输入服务年限" },
                                          {
                                              // pattern: /^\+?(?:0|[1-9]\d*)(?:\.\d)$/,
                                              // message:"只保留一位浮点数！"
                                          }
                                      ],
                                  })(
                                      <Input/>
                                  )}
                              </FormItem>
                              </div>
                          {/*</Form>*/}
                      {/*</div>*/}
                      <Divider dashed={true} style={{borderColor:"#BBBBBB"}} />
                      {/*<div className={styles.center}>*/}
                              <div className={styles.top}>
                          <p className={styles.title}>选填信息</p>
                          {/*<Form>*/}
                          <FormItem {...formItemLayout} label="实际并发用户比">
                          {getFieldDecorator('actualConcurrentUserRatio', {
                              initialValue:0.6
                          })(
                          <InputNumber min={0} max={1} placeholder="0~1" style={{width:"100%"}}/>
                          )}
                          </FormItem>
                          <span className={styles.prompt}>单位:G </span>
                          <FormItem {...formItemLayout} label="用户存储配额">
                          {getFieldDecorator('userStorageQuota', {
                              initialValue:2
                          })(
                          <Input placeholder="2"/>
                          )}
                          </FormItem>
                          <FormItem {...formItemLayout} label="CPU服务器数">
                          {getFieldDecorator('CPUServersNumber', {
                              rules: [
                                  {
                                      pattern: /^\+?(?:0|[1-9]\d*)$/,
                                      message:"请输入整数！"
                                  }
                              ],
                              initialValue:1
                          })(
                          <InputNumber min={1} max={7} placeholder="1~7" style={{width:"100%"}}/>
                          )}
                          </FormItem>
                          {
                          item.id==="EXCLUSIVE"?
                          (
                          <div>
                          <span className={styles.prompt}>默认：0元/Mbps/年</span>
                          <FormItem {...formItemLayout} label="网络宽带价格">
                          {getFieldDecorator('bandWidthPrice', {
                              initialValue:0
                          })(
                          <Input placeholder="0"/>
                          )}
                          </FormItem>
                          <span className={styles.prompt}>默认：(10 * 总价)元/年</span>
                          <FormItem {...formItemLayout} label="运维价格">
                          {getFieldDecorator('opsPrice', {
                              initialValue:10
                          })(
                              <InputNumber
                                  placeholder="10"
                                  min={0}
                                  max={100}
                                  formatter={value => `${value}%`}
                                  parser={value => value.replace('%', '')}
                                  style={{width:"100%"}}
                              />
                          )}
                          </FormItem>
                          </div>
                          ):(
                          <div>
                          <span className={styles.prompt}>默认：120元/Mbps/年</span>
                          <FormItem {...formItemLayout} label="网络宽带价格">
                          {getFieldDecorator('bandWidthPrice', {
                              initialValue:120
                          })(
                          <Input   placeholder="120"/>
                          )}
                          </FormItem>
                          <span className={styles.prompt}>默认：132288元/年</span>
                          <FormItem {...formItemLayout} label="机柜租赁">
                          {getFieldDecorator('CabinetRental', {
                              initialValue:"132288"
                          })(
                          <Input  placeholder="132288"/>
                          )}
                          </FormItem>
                          <span className={styles.prompt}>默认：7470 元/年</span>
                          <FormItem {...formItemLayout} label="运维人力">
                          {getFieldDecorator('OperationAndMaintenanceManpower', {
                              initialValue:"7470"
                          })(
                          <Input placeholder="7470"/>
                          )}
                          </FormItem>
                          </div>
                          )
                          }
                  </div>
                          </Form>

                      <Divider dashed={true} style={{borderColor:"#BBBBBB"}}/>
                      <div className={styles.bottom}>
                          <p className={styles.title}>配置项</p>
                          <span className={styles.configure} >配置信息:</span>

                          <Table
                              columns={this.state.columns}
                              dataSource={this.state.dataSource}
                              onChange={this.handleChange} pagination={false}
                              className={styles.table} size="middle"
                              loading={this.state.tableLoading}
                              rowKey={(record) => `${record.key}`}
                          >
                          </Table>

                      </div>
                      {/*配置详情弹出框*/}
                      <Modal
                          title="配置详情"
                          visible={this.state.detailVisible}
                          onOk={this.detailClickOk}
                          okText="确认"
                          cancelText="取消"
                          onCancel={this.detailClickCancel}
                          width={960}
                          bodyStyle={{height:"549px",padding:"24px 54px"}}
                      >
                          <div>
                              <p>
                                  <span style={{display:"inline-block",width:"86.34px",textAlign:"right",color:"#666666"}}>{this.state.detailRecord.equipment}：</span>
                                  <span>{this.state.selectValueName}</span>
                                  {/*<span>{this.state.detailRecord.configure[this.state.selectValue].name}</span>*/}
                              </p>
                              <p><span style={{display:"inline-block",width:"85px",textAlign:"right",color:"#666666"}}>
                        售价：
                    </span>
                                  <span style={{color:"#6597FF"}}>
                        {this.state.detailPrice}元/{this.state.detailRecord.equipment==="硬盘"?"块":"台"}
                    </span>
                              </p>
                              <div>
                        <span style={{display:"inline-block",width:"85px",textAlign:"right",verticalAlign:"top",color:"#666666"}}>
                            详细信息：
                        </span>
                                  <div   style={{display:"inline-block",width:"655px",height:"384px",padding:"10px",overflowY:"auto",border:"1px solid #BBBBBB"}}>
                                      {this.state.aSeverParts.length>0?(
                                          this.state.aSeverParts.map((item,index)=>{return(<p key={index}>
                                              {item.partByPartId.name.indexOf("支持")=="-1" ?item.partNumber:""}{item.partNumber!=="" && item.partNumber!==null && item.partByPartId.name.indexOf("支持")=="-1" ?"x":""}{item.partByPartId.name}
                                          </p>)})):(<div style={{textAlign:"center",width:"635px",height:"354px",lineHeight:"354px",color:"#BBBBBB"}} >
                                          <span>暂无数据</span>
                                      </div>)}
                                  </div>
                              </div>
                          </div>

                      </Modal>
                      {/*查看询单弹出层*/}
                      <QueryFormPopup
                          addQueryForm={this.state.addQueryForm}
                          queryFormModalVisible={this.state.queryFormModalVisible}
                          modalChange={newState=>{this.queryFormModalChange(newState)}}
                          orderInfo={this.state.orderInfo}
                          hardwareTopologyUrl={this.state.hardwareTopologyUrl}
                          tabId={this.state.tabData[this.state.tabKey].id}
                          fieldsValue={this.state.fieldsValue}
                      />
                  </div>
              </TabPane>)
                })}
            </Tabs>

        </div>

);
}
}




export default Form.create()(Home);
